import React from 'react';
const Headings = ({ number, title, emoji }) => {
  return (
    <h1 className=' is-size-2 is-size-3-mobile has-text-black'>
      {' '}
      <span className='number-about is-family-mono'>{number}</span>
      <span className='hr-match is-lowercase	'>{title}</span>
      <span
        role='img'
        aria-label={title}
        className='is-size-3 ml-3 is-size-4-mobile is-vcentered'
      ></span>{' '}
    </h1>
  );
};
export default Headings;
